// app/events/AddEventModal.tsx
"use client";

import { useEffect, ChangeEvent } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card } from "@/components/ui/card";
import { X } from "lucide-react";

interface EventData {
  eventName: string;
  subtitle: string;
  hostedBy: string;
  date: string;
  time: string;
  fullAddress: string;
  city: string;
  category: string;
  maxCapacity: string;
  about: string;
  partyFlow: string;
  partyEtiquette: string;
  houseRules: string;
  cancellationPolicy: string;
  ageRestriction: string;
}

interface AddEventModalProps {
  open: boolean;
  onClose: () => void;
  initialData?: EventData | null;
}

export default function AddEventModal({
  open,
  onClose,
  initialData,
}: AddEventModalProps) {
  const defaultForm: EventData = {
    eventName: "",
    subtitle: "",
    hostedBy: "",
    date: "",
    time: "",
    fullAddress: "",
    city: "",
    category: "",
    maxCapacity: "",
    about: "",
    partyFlow: "",
    partyEtiquette: "",
    houseRules: "",
    cancellationPolicy: "",
    ageRestriction: "",
  };

  const [form, setForm] = useState<EventData>(defaultForm);

  // Sync form with initialData when editing or when modal opens
  useEffect(() => {
    if (open) {
      setForm(initialData ?? defaultForm);
    }
  }, [initialData, open]);

  const handleChange = (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = () => {
    if (initialData) {
      console.log("Updating event:", form);
      // TODO: Call your update API
    } else {
      console.log("Creating new event:", form);
      // TODO: Call your create API
    }
    onClose();
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 p-4">
      <Card className="w-full max-w-4xl max-h-[90vh] overflow-y-auto bg-background p-8 shadow-2xl">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <h2 className="text-3xl font-bold">
            {initialData ? "Edit Event" : "Create New Event"}
          </h2>
          <button
            onClick={onClose}
            className="rounded-full p-2 hover:bg-muted transition-colors"
            aria-label="Close modal"
          >
            <X className="h-6 w-6" />
          </button>
        </div>

        {/* Form Grid */}
        <div className="space-y-8">
          {/* Basic Info */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <Label htmlFor="eventName">Event Name <span className="text-red-500">*</span></Label>
              <Input
                id="eventName"
                name="eventName"
                value={form.eventName}
                onChange={handleChange}
                placeholder="e.g. Summer Rooftop Party"
                required
              />
            </div>

            <div>
              <Label htmlFor="subtitle">Subtitle</Label>
              <Input
                id="subtitle"
                name="subtitle"
                value={form.subtitle}
                onChange={handleChange}
                placeholder="A catchy tagline (optional)"
              />
            </div>

            <div>
              <Label htmlFor="hostedBy">Hosted By <span className="text-red-500">*</span></Label>
              <Input
                id="hostedBy"
                name="hostedBy"
                value={form.hostedBy}
                onChange={handleChange}
                placeholder="Host name or organization"
              />
            </div>

            <div>
              <Label htmlFor="category">Category</Label>
              <Input
                id="category"
                name="category"
                value={form.category}
                onChange={handleChange}
                placeholder="e.g. Party, Workshop, Concert"
              />
            </div>

            <div>
              <Label htmlFor="date">Date <span className="text-red-500">*</span></Label>
              <Input
                id="date"
                type="date"
                name="date"
                value={form.date}
                onChange={handleChange}
              />
            </div>

            <div>
              <Label htmlFor="time">Time <span className="text-red-500">*</span></Label>
              <Input
                id="time"
                type="time"
                name="time"
                value={form.time}
                onChange={handleChange}
              />
            </div>
          </div>

          {/* Location */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <Label htmlFor="fullAddress">Full Address <span className="text-red-500">*</span></Label>
              <Input
                id="fullAddress"
                name="fullAddress"
                value={form.fullAddress}
                onChange={handleChange}
                placeholder="Street, building, apartment"
              />
            </div>

            <div>
              <Label htmlFor="city">City <span className="text-red-500">*</span></Label>
              <Input
                id="city"
                name="city"
                value={form.city}
                onChange={handleChange}
                placeholder="e.g. Mumbai"
              />
            </div>
          </div>

          {/* Extra Details */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <Label htmlFor="maxCapacity">Max Capacity</Label>
              <Input
                id="maxCapacity"
                name="maxCapacity"
                type="number"
                min="1"
                value={form.maxCapacity}
                onChange={handleChange}
                placeholder="e.g. 100"
              />
            </div>

            <div>
              <Label htmlFor="ageRestriction">Age Restriction</Label>
              <Input
                id="ageRestriction"
                name="ageRestriction"
                value={form.ageRestriction}
                onChange={handleChange}
                placeholder="e.g. 18+, All ages"
              />
            </div>
          </div>

          {/* Text Areas */}
          <div className="space-y-6">
            <div>
              <Label htmlFor="about">About the Event</Label>
              <textarea
                id="about"
                name="about"
                value={form.about}
                onChange={handleChange}
                rows={5}
                className="w-full rounded-md border border-input bg-transparent px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2"
                placeholder="Tell guests what to expect..."
              />
            </div>

            <div>
              <Label htmlFor="houseRules">House Rules</Label>
              <textarea
                id="houseRules"
                name="houseRules"
                value={form.houseRules}
                onChange={handleChange}
                rows={4}
                className="w-full rounded-md border border-input bg-transparent px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2"
                placeholder="e.g. No smoking, respect neighbors..."
              />
            </div>

            <div>
              <Label htmlFor="cancellationPolicy">Cancellation Policy</Label>
              <textarea
                id="cancellationPolicy"
                name="cancellationPolicy"
                value={form.cancellationPolicy}
                onChange={handleChange}
                rows={4}
                className="w-full rounded-md border border-input bg-transparent px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2"
                placeholder="Refund policy, deadlines, etc."
              />
            </div>
          </div>
        </div>

        {/* Actions */}
        <div className="flex justify-end gap-4 mt-10">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={handleSubmit}>
            {initialData ? "Update Event" : "Create Event"}
          </Button>
        </div>
      </Card>
    </div>
  );
}